import React from "react";
import "./s.css";
import target from "./target.png"

//top = 0, 880
//left = 0, 1600
//ToDo: 
//some timer 
//change button to image
//add background
//cap size?

class App extends React.Component {

  constructor() {
    super();
    this.state = {
      top: (window.innerHeight / 2),
      left: (window.innerWidth / 2),
      count: 0
    };
    this.onClick = this.move.bind(this);
    this.playAgain = this.playAgain.bind(this);
  }


  //when target is hit, change position and add to score count
  move() {
    this.setState({
      top: (Math.random() * (window.innerHeight - 0)),
      left: (Math.random() * (window.innerWidth - 0)), //sub based on target size
      count: this.state.count + 1
    });
  }

  //restart 
  playAgain() {
    this.setState({
      top: (window.innerHeight / 2),
      left: (window.innerWidth / 2),
      count: 0
    });
  }

  render() {
    //win the game if a score cap is set
    if (this.state.count === 100) {
      //console.log("winner")
      return (
        <div>
          <h1> WINNER </h1>
          <button onClick={this.playAgain}> PLAY AGAIN </button>
        </div>

      )
    }

    const button = {
      //     backgroundColor: "#FF2D00",
      height: 75,
      width: 75,
      //    fontSize: 32,
      position: 'absolute',
      left: this.state.left,
      bottom: this.state.top
    }

    return (
      <div>
        <img style={button} onClick={this.onClick} src={target} alt="target" />
        <h1 className="score">Score: {this.state.count}</h1>
      </div >
    )
  };
}

export default App;
